.App {
  padding: 20px;
  text-align: left;
  overflow: hidden;
  /* position: relative; */
}
.App strong a {
  color: darkslategray;
  text-decoration: none;
  font-size: 30px;
}
.App-inner {
  transition: 500ms height ease;
  overflow: hidden;
  position: relative;
}
.App-inner2 {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.read-more-btn {
  background: darkslategray;
  color: #fff;
  padding: 15px 25px;
  outline: none;
  border: none;
  box-shadow: 0px 0px 10px rgb(0 0 0);
  border-radius: 4px;
  cursor: pointer;
  transition: 300ms all ease;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 20px;
  letter-spacing: 2px;
  margin-top: 20px;
}
.read-more-btn:hover {
  background-color: #fff;
  color: darkslategray;
}
